<template>
  <div class="detail">
    <van-swipe class="my-swipe" @change="onChange">
      <van-swipe-item>1</van-swipe-item>
      <van-swipe-item>2</van-swipe-item>
      <van-swipe-item>3</van-swipe-item>
      <van-swipe-item>4</van-swipe-item>
      <template #indicator>
        <div class="custom-indicator">{{ current + 1 }}/4</div>
      </template>
    </van-swipe>
    <!-- 商品介绍-->
    <div class="product-intro border-bottom-sm">
      <div class="summary-first">
        <div>
          <span>798.5</span>
          <span>899.68</span>
        </div>
        <div>最低采购量：100</div>
      </div>
      <div class="sku-name padding-lr">2020夏季最新热卖爆款小星星项链韩版流行s925银 灵动女款锁骨链简约百搭</div>
      <div class="col-l padding-lr">
        <div>物流</div>
        <div class="gray1">浙江 杭州</div>
      </div>
      <div class="col-l padding-lr border-top-sm">
        <div>服务</div>
        <div>4小时包换 · 交期保障 · 运费险</div>
      </div>
    </div>

    <div class="user-group border-bottom-sm">
      <group></group>
    </div>
    <div class="appraise-wrap border-bottom-sm">
      <appraise></appraise>
    </div>
    <!-- 商品参数-->

    <div class="p-parameter padding-lr border-bottom-sm">
      <div class="p-title">商品信息</div>
      <ul class="parameter-list">
        <li>
          <div>尺码</div>
          <div>S M L XL</div>
        </li>
        <li>
          <div>尺码</div>
          <div>S M L XL</div>
        </li>
      </ul>
      <div class="look-all" @click="openAll">
        {{ lookAll ? "收起全部" : "展开全部" }}
        <img v-lazy="allIcon" alt :class="[lookAll ? 'bottom' : '']" />
      </div>
    </div>

    <!-- 商品承诺-->
    <div class="summary-tips"></div>
    <!-- 商品评价区域 -->

    <!-- 商家货品 -->

    <goods></goods>
    <div class="detail-content border-top-sm"></div>
    <!--弹出层 -->
    <pop v-model="showPop"></pop>
    <goods-action @change="onOpenOrder" />
  </div>
</template>
<script>
import { Swipe, SwipeItem } from "vant";
export default {
  data() {
    return {
      rightIcon: require("@/assets/more@2x.png"),
      allIcon: require("@/assets/all@2x.png"),
      showPop: false,
      current: 0,
      lookAll: false
    };
  },
  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    group: () => import("./components/group.vue"),
    appraise: () => import("./components/appraise.vue"),
    goods: () => import("./components/goods.vue"),
    pop: () => import("./components/pop.vue"),
    goodsAction: () => import("./components/action.vue")
  },
  methods: {
    onChange(index) {
      this.current = index;
    },
    onOpenOrder() {
      this.showPop = !this.showPop;
    },
    openAll() {
      this.lookAll = !this.lookAll;
    }
  }
};
</script>
<style lang="scss">
$priceColor: #ff3b37; //价格色值
$stepColor: #fe851e; //起批量色值
$fontColor: $defaultFontColor; //字体色值
$gray1: $defaultGray; //灰色
$p-gray: #918e8f; //商品信息灰色字体
$p-borderAllColor: #efefef; //展开全部边框色值
$p-AllColor: #aeaeae; //展开全部边框色值
$bg: #fcf9fc; //模块间距色值

.gray1 {
  color: $gray1;
}

.detail {
  @include font-size(28);
  .my-swipe {
    height: 750px;
    background: #eeeeee;
    .van-swipe-item {
      color: #fff;
      font-size: 20px;
      line-height: 150px;
      text-align: center;
      background-color: #39a9ed;
    }
  }
  // 轮播指示器
  .custom-indicator {
    position: absolute;
    right: 30px;
    bottom: 35px;
    width: 78px;
    height: 40px;
    background: rgba(255, 255, 255, 0.5);
    border-radius: 20px;
    color: $fontColor;
    @include flex;
    @include font-size(28);
  }
  .product-intro {
    .summary-first {
      @include flex(space-between);
      @include font-size(28);
      line-height: 1;
      padding: 34px 30px 28px;
      > div:first-child {
        span {
          &:first-child {
            @include font-size(48);
            color: $priceColor;
            &::before {
              content: "￥";
              @include font-size(34);
            }
          }
          &:nth-child(2) {
            color: $fontColor;
            text-decoration: line-through;
            margin-left: 18px;
          }
        }
      }
      > div:nth-child(2) {
        color: $stepColor;
        @include font-size(28);
      }
    }
    .sku-name {
      @include font-size(30);
      @include text-column(2);

      line-height: 45px;
      color: $fontColor;
      font-weight: 600;
    }
    .col-l {
      display: flex;
      > div {
        line-height: 1;
        padding: 20px 0 25px;
        &:first-child {
          width: 115px;
          justify-content: flex-start;
        }
      }
    }
  }
  //规格参数
  .p-parameter {
    padding-bottom: 30px;
    .p-title {
      line-height: 1;
      padding: 33px 0 26px;
    }
    .parameter-list {
      li {
        display: flex;
        > div {
          @include text-column(1);
          margin-bottom: 25px;
          &:first-child {
            max-width: 169px;
            min-width: 169px;
            padding-right: 30px;
            box-sizing: border-box;
            color: $p-gray;
          }
        }
      }
    }
    .look-all {
      width: 210px;
      height: 52px;
      border: 2px solid $p-borderAllColor;
      border-radius: 10px;
      margin: 0 auto;
      color: $p-AllColor;
      line-height: 1;
      @include flex;
      img {
        margin-left: 10px;
        width: 34px;
        height: 34px;

        &.bottom {
          transform: rotate(-180deg);
        }
      }
    }
  }
}

$bg: #ddd;
$gray: #8e8c8f;
$red: #f14745;
.group {
  .group-party {
    height: 95px;
    @include flex(space-between);
    .gray1 {
      line-height: 1;
      @include iflex;

      img {
        width: 34px;
        height: 34px;
      }
    }
  }

  .group-list {
    li {
      padding: 30px 0;
      @include flex;
      .user-photo {
        width: 88px;
        height: 88px;
        background: $bg;
        img {
          width: 100%;
        }
      }
      .user-name {
        margin-left: 30px;

        > div {
          &:first-child {
            @include font-size(30);
            @include text-column(1);
            width: 160px;
            margin-bottom: 9px;
          }
          .van-count-down {
            color: $gray;
            line-height: 1;
            @include font-size(22);
          }
        }
      }
      .schedule {
        margin: 0 55px 0 38px;
        flex: 1;
        .van-progress {
          background: #efefef;
          height: 30px;
          border-radius: 15px;
        }
      }
      .join {
        min-width: 120px;
        button {
          @include flex;
          @include font-size(26);
          width: 120px;
          height: 54px;
          background: $red;
          border-radius: 10px;
          color: #fff;
        }
      }
    }
  }
}
</style>
